
.images {
  height: 5.64rem;
  background: red;
}
.mian-con {
  padding: 0.4rem;
  margin-bottom: 1.6rem;
  /*tabs*/
}
.mian-con h3 {
    line-height: 0.3rem;
    font-size: 0.32rem;
    height: 0.3rem;
}
.mian-con .s-contain {
    background: #ffffff;
}
.mian-con .s-contain .s-contain-title .tabs {
      width: 2.64rem;
      margin: 0 auto;
}
.mian-con .s-contain .s-contain-title .top-tabs {
      border: 1px solid #95918f;
      width: 1.32rem;
      text-align: center;
      height: 0.55rem;
      color: #1b0f0f;
      line-height: 0.55rem;
}
.mian-con .s-contain .s-contain-title .top-tabs:first-child {
        border-right: none;
        border-bottom-left-radius: 0.05rem;
        border-top-left-radius: 0.05rem;
}
.mian-con .s-contain .s-contain-title .top-tabs:last-child {
        border-bottom-right-radius: 0.05rem;
        border-top-right-radius: 0.05rem;
}
.mian-con .s-contain .s-contain-title .top-tabs.isSelected {
        background: #95918f;
        color: #fff;
}
.mian-con .informations {
    padding-top: 0.1rem;
    font-size: 0.26rem;
    color: #aaaeb6;
}
.warp {
  position: relative;
}
.bot-fixed {
  position: fixed;
  bottom: 1.1rem;
  height: 0.9rem;
  width: 100%;
  border-top: 1px solid #f3f3f4;
  line-height: 0.9rem;
  padding: 0 0.45rem;
  background: pink;
  color: #3b4655;
}
.bot-fixed p {
    display: inline-block;
    height: 0.9rem;
}
.bot-fixed p:first-child {
      float: left;
}
.bot-fixed p:last-child {
      float: right;
      margin: 0.2rem 0;
      height: 0.5rem;
      width: 0.9rem;
      background: #ff5e3a;
      color: #fff;
      text-align: center;
      line-height: 0.5rem;
      border-radius: 0.5rem;
      font-size: 0.26rem;
}
.mint-popup {
  border-radius: 8px;
  overflow: hidden;
}
.tel-dialog {
  width: 5.5rem;
  background: #fff;
  padding: 0.6rem 0.2rem;
}
.tel-dialog div {
    text-align: center;
}
.tel-dialog .img_bg {
    height: 0.5rem;
    background: url("../../../static/img/dao.png") no-repeat center center;
    background-size: contain;
}
.tel-dialog .title_lg {
    font-size: 0.3rem;
    padding-top: .1rem;
}
.tel-dialog .title_sm {
    color: #666;
    padding: 10px;
}
.tel-dialog .mint-cell-title {
    width: 55px !important;
}
.tel-dialog .code_btn {
    background: #ff5e3a;
    border-radius: 5px;
    padding: 5px 15px;
    color: #fff;
    font-size: 10px;
}
.tel-dialog p {
    padding: 5px;
    background: #ff5e3a;
    color: #fff;
    text-align: center;
    border-radius: 50px;
    margin-top: 0.2rem;
}



