
@charset "UTF-8";
#mescroll {
  position: absolute;
  top: 0;
  bottom: 2rem;
  left: 0;
  right: 0;
  height: auto;
  overflow-y: auto;
}
.item_box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.item_box .item_top {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.item_box .item_top .item_right {
      color: #999;
      font-size: 10px;
}
.item_box .item_top .item_left {
      position: relative;
      padding-left: 5px;
}
.item_box .item_top .item_left p {
        position: absolute;
        left: 0;
        top: 5%;
        border-left: 2px solid #ff5232;
        height: 80%;
}
.item_box .item_bottom {
    color: #333;
    font-size: 10px;
}

/*修正全剧样式*/
.contain {
  margin: 0 0 !important;
}
.mint-badge.is-size-small {
  padding: 1px 6px;
  font-size: 10px;
}
.index {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.informations {
  padding: 0.4rem;
}
.zhibo_img {
  width: 100%;
  margin-top: 0.5rem;
}
.mint-tab-container-wrap {
  height: 100%;
}
.s-index-header {
  width: 100%;
  height: 0.94rem !important;
  line-height: 0.94rem !important;
  background: -webkit-linear-gradient(top, #02429b, #297ae8);
  color: #fff;
  font-size: 0.33rem;
  position: fixed;
  top: 0;
  z-index: 999;
}
.s-index-header img {
    height: 0.6rem;
    width: 0.6rem;
    float: left;
    margin: 0.16rem;
}
.s-index-header h3 {
    line-height: 0.94rem;
    text-align: center;
    margin-right: 0.8rem;
    font-weight: normal;
}
.s-index-contain {
  color: #8a8684;
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.s-index-contain .div_bottom {
    -ms-flex: auto;
        flex: auto;
    overflow-y: auto;
    padding-bottom: 1.1rem;
    position: relative;
}
.s-index-contain .video-wrap {
    width: 7.5rem;
    height: 5rem;
    background: rgba(0, 0, 0, 0.1);
    position: relative;
}
.s-index-contain .video-wrap .model {
      width: 7.5rem;
      height: 5rem;
      position: absolute;
      left: 0;
      top: 0;
      background: rgba(0, 0, 0, 0.5);
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center;
      -ms-flex-align: center;
          align-items: center;
}
.s-index-contain .video-wrap .model .play {
        width: 0.65rem;
        height: 0.65rem;
        background: url("../../../static/img/play.png") no-repeat center center;
        background-size: contain;
}
.s-index-contain .video-wrap .share {
      display: inline-block;
      /*width: 1.08rem;*/
      padding: 0 0.2rem;
      height: 0.6rem;
      background: #ff5333;
      line-height: 0.6rem;
      color: #fff;
      border-radius: 0.25rem;
      position: absolute;
      z-index: 10;
      right: 0.2rem;
      bottom: 0.2rem;
      text-align: center;
}
.s-index-contain .video-wrap .share .icon-share {
        display: inline-block;
        width: 0.28rem;
        height: 0.28rem;
        background: url("../../../static/img/share.png") no-repeat;
        background-size: contain;
}
.s-index-contain .mint-navbar {
    box-shadow: 0 3px 3px rgba(255, 83, 51, 0.1);
}
.s-index-contain .mint-tab-container {
    /*max-height: 4.5rem;*/
    overflow: scroll;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.s-index-contain .mint-tab-item {
    margin: 0 0.5rem;
    color: #8a8684;
}
.s-index-contain .mint-navbar .mint-tab-item.is-selected {
    color: #191111;
    font-weight: bold;
    margin-bottom: 0;
    border-bottom: 3px solid #ff5232;
}
.s-index-contain .nav-content {
    /*问答区域*/
}
.s-index-contain .nav-content .dialog {
      padding: 0.2rem 0;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.s-index-contain .nav-content .dialog .user {
        margin: 0.2rem;
}
.s-index-contain .nav-content .dialog .dia-content {
        max-width: 4.7rem;
        padding: 0.2rem;
        line-height: 0.4rem;
        background: #eeeff1;
        border-radius: 0.2rem;
        color: #322c2c;
}
.s-index-contain .nav-content .dialog .dia-content .student {
          border-bottom: 1px dashed #dadadb;
          padding-bottom: 0.1rem;
}
.s-index-contain .nav-content .dialog .dia-content .teacher {
          padding-top: 0.1rem;
}
.s-index-contain .nav-content .dialog .dia-content .time {
          font-size: 0.22rem;
          text-align: right;
          padding-top: 0.07rem;
}
.s-index-contain .nav-content .s_dialog {
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
}
.s-index-contain .nav-content .s_dialog .dia-content {
        background: #0096e5;
        color: #fff;
}
.s-index-contain .nav-content .ask {
      width: 7.5rem;
      background: #fff;
      border-top: 1px solid #f8f8f9;
      /*height: 0.88rem;*/
      position: fixed;
      bottom: 1.1rem;
      padding-top: 0.1rem;
      padding-bottom: 0.1rem;
      z-index: 999;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: distribute;
          justify-content: space-around;
}
.s-index-contain .nav-content .ask input {
        width: 4.44rem;
        height: 0.68rem;
        border-radius: 0.2rem;
        background: #f8f8f9;
        border: none;
        outline: none;
        margin-left: 0.2rem;
        padding-left: 0.2rem;
}
.s-index-contain .nav-content .ask .btn {
        width: 0.6rem;
        height: 0.6rem;
        border-radius: 50%;
}
.s-index-contain .nav-content .ask #money {
        background: url("../../../static/img/money.png") no-repeat;
        background-size: contain;
}
.s-index-contain .nav-content .ask #thumbsUp {
        background: url("../../../static/img/thumb.png") no-repeat;
        background-size: contain;
}
.s-index-contain .nav-content .ask #send {
        background: url("../../../static/img/send.png") no-repeat;
        background-size: contain;
}



