
.m-tabbar {
  background: #f8f8f8 !important;
  height: 1.1rem !important;
}
.m-tabbar i {
    display: block;
    font-size: .40rem;
}
.m-tabbar i.phone {
    display: block;
    font-size: .43rem;
    margin-top: -.02rem;
}
.s-router {
  height: 1.1rem !important;
  position: fixed;
  bottom: 0;
  background: #f6f6f6;
  width: 100%;
  z-index: 999;
  border-top: 1px solid #a1b2ba;
}
.s-router li {
    float: left;
    width: 25%;
    line-height: .55rem;
    text-align: center;
    padding-top: .08rem;
}
.s-router li i {
      display: block;
      font-size: .50rem;
}
.s-router li span {
      margin-top: -.05rem;
      display: block;
      font-size: .26rem;
}
.s-router li span:nth-child(2n+1) {
      margin-top: .05rem;
}
.s-router li img {
      height: .5rem;
}
.s-router li:nth-child(3) i {
    /*font-size: 0.57rem;
			margin-bottom: 0.05rem*/
}
.activecheck {
  color: #ff5e3a;
}
.noactivecheck {
  color: #6d6d6d;
}
i.icon-global:before {
  display: block;
  margin-top: -.05rem;
}



